last-child td {\r\n  border-bottom: 1px solid var(--gray-3);\r\n}\r\n\r\nth {\r\n  text-align: left;\r\n  font-weight: normal;\r\n  color: var(--gray-2);\r\n  white-space: nowrap;\r\n}\r\n\r\nth > div {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\ntd.actions-cell > * {\r\n  margin: auto;\r\n}\r\n\r\na {\r\n  color: var(--blue-1);\r\n  text-decoration: none;\r\n}\r\n\r\n.link-wrap {\r\n  display: inline-block;\r\n  padding-bottom: 1px;\r\n  border-bottom: 1px dashed var(--blue-1);\r\n  display: inline-block;\r\n}\r\n\r\n.icon {\r\n  display: inline-block;\r\n  transform: translateY(2px);\r\n  margin-right: 0.3rem;\r\n}\r\n\r\n.table-title {\r\n  font-weight: 600;\r\n  font-size: 15px;\r\n}\r\n\r\n.heading {\r\n  margin-bottom: 0.5rem;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  flex-wrap: wrap;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
